export default {
	name: "article-item",
	props: {
		list: {
			type: Array,
			default () {
				return []
			}
		},
	},
	data() {
		return {
			showArticleOperate: false,
			articleId: '',
			articleOffsetTop: 0,
		};
	},
	methods: {
		showMoreMask(e,articleId,userId,articleIndex) {
			this.$emit('showOperate', e,articleId,userId,articleIndex)
		},
		hideArticleOperate() {
			this.showArticleOperate = false;
		},
		goDetail(id) {
			uni.navigateTo({
				url: '/pages/home/article/detail/detail?id=' + id
			})
		}
	}
}
